/* Welcome.css */

/* Apply to the whole page with background image */
body {
  margin: 0;
  padding: 0;
  font-family: 'Arial', sans-serif;
  background: 
    linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), /* Dark overlay for readability */
    url('./welcom.jpeg'); /* Image is in the same folder as Welcome.css */
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white; /* White text for contrast */
}

/* Centering the content inside Grid */
.welcome-container {
  padding: 30px;
  min-height: 93vh;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  flex-direction: column;
}

/* Typography styling */
h2 {
  color: #fff; /* White text */
  font-size: 3rem;
  margin: 0;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.6); /* Shadow for readability */
}

/* Responsive for mobile */
@media (max-width: 768px) {
  h2 {
    font-size: 2rem;
  }
}
